<template>
	<div>
		<el-container>
			<el-header class="homeHeader">
				<div class="title">微人事管理系统</div>
				<div>
					<el-button icon="el-icon-bell" @click="goChat" type="text" style="margin-right: 12px;color: black;"
							   size="normal"></el-button>
					<el-dropdown class="userInfo" @command="commandHandler">
  <span class="el-dropdown-link">
    {{ user.name }}<i><img :src="user.userface" alt=""></i>
  </span>
						<el-dropdown-menu slot="dropdown">
							<el-dropdown-item command="userinfo">个人中心</el-dropdown-item>
							<el-dropdown-item command="setting">设置</el-dropdown-item>
							<el-dropdown-item command="logout" divided>注销登录</el-dropdown-item>
						</el-dropdown-menu>
					</el-dropdown>
				</div>
			</el-header>
			<el-container>
				<el-aside width="200px">
					<!--					<el-menu @select="menuClick">-->
					<el-menu router unique-opened>
						<el-submenu :index="index+''" v-for="(item,index) in routes" v-if="!item.hidden"
									:key="index">
							<!--							for循环的index要注明出来-->
							<template slot="title">
								<i style="color:#409eff;margin-right:5px" :class="item.iconCls"></i>
								<span>{{ item.name }}</span>
							</template>
							<el-menu-item :index="child.path" v-for="(child,indexj) in item.children"
										  :key="indexj">
								{{ child.name }}
							</el-menu-item>
						</el-submenu>
					</el-menu>
				</el-aside>
				<el-main>
					<el-breadcrumb separator-class="el-icon-arrow-right" v-if="this.$router.currentRoute.path!='/home'">
						<el-breadcrumb-item :to="{path: '/home' }">首页</el-breadcrumb-item>
						<el-breadcrumb-item>{{ this.$router.currentRoute.name }}</el-breadcrumb-item>
					</el-breadcrumb>
					<div class="homeWelcome" v-if="this.$router.currentRoute.path =='/home'">
						欢迎来到微人事管理系统
					</div>
					<router-view class="homeRouterView"/>
				</el-main>
			</el-container>
		</el-container>

	</div>
</template>

<script>
export default {
	name: "Home",
	data() {
		return {
			 // user: JSON.parse(window.sessionStorage.getItem("user"))
		}
	},
	computed: {
		routes() {
			return this.$store.state.routes;
		},
		user(){
			return this.$store.state.currentHr;
		},
	},
	methods: {
		goChat() {
			this.$router.push("/chat");
		},
		// menuClick(index){
		// 	this.$router.push(index);
		// 	// console.log(index)
		// 	// console.log(indexPath)
		// },
		commandHandler(cmd) {
			if (cmd == 'logout') {
				this.$confirm('此操作将注销登录, 是否继续?', '提示', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning'
				}).then(() => {
					this.getRequest("/logout");
					window.sessionStorage.removeItem("user");
					this.$store.commit('initRoutes', [])
					this.$router.replace("/");

				}).catch(() => {
					this.$message({
						type: 'info',
						message: '已取消注销操作'
					});
				});
			}else if(cmd=='userinfo'){
				this.$router.push('/hrinfo');

			}

		}
	}
}
</script>

<style>
.homeRouterView {
	margin-top: 10px;
}

.homeWelcome {
	text-align: center;
	font-size: 48px;
	font-family: 华文行楷;
	color: #409eff;
	padding-top: 50px;
}

.homeHeader {
	background-color: #409eff;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0px 15px;
	box-sizing: border-box;
}

.homeHeader .title {
	font-size: 30px;
	color: #ffffff;
	font-family: 华文行楷;
}

.homeHeader .userInfo {
	cursor: pointer;
}

.el-dropdown-link img {
	width: 48px;
	height: 48px;
	border-radius: 24px;
	margin-left: 8px;
}

.el-dropdown-link {
	display: flex;
	align-items: center;
}
</style>
